@media only screen and (max-width: 484px) {
    .simple-menu{
        display: block;
        position: fixed;
        left: 0;
        top: 20px;
        z-index: 999999;
        border: 1px solid grey;
        background-color: rgba(165, 165, 165, 0.7);
    }
    .nav{
        display: none;
    }
    .links{
        font-size: 0.8rem;
    }
    .nav__language{
        display: none!important;
    }
    .phoneChangeLang{
        display: flex;
        justify-content: center;
        align-items: center;
        z-index: 999999;
    }
    .phoneChangeLang p{
        margin-top: 10px;
    }
}
