@media only screen and (max-width: 484px) {

    .wrapLink,.wrapLinkTwo{
        height: 200px !important;
    }
    .sliderWrap img{
        width: 100%;
        height: 400px ;
    }
    .works__picture{

    }
    .imageSize{
        margin-right: 0;
        width: 100%;
    }
    .imageSize img{
        width: 100%;
        height: 400px;
    }

    .wrap__item{
        justify-content: space-between;
    }
    .link__item{
        font-size: 13px;

    }
    .sliderWrap{
        width: 90vw;
        height: auto;

    }
    .slick-arrow{
        display: none !important;

    }


}
@media only screen and (max-width: 1200px) {
    .sliderWrap{
        width: 90vw;
    }
    .wrapLink,.wrapLinkTwo{
        height: 400px;
    }
}