@media only screen and (min-width: 320px) {

    .smallPhone{
        margin-top: 470px;
    }
    .detail{
        margin-top: 0 !important;
    }
    .instagram_wrap{
        margin-bottom: 20px !important;
    }

}

@media only screen and (min-width: 375px) {
    .smallPhone{
        margin-top: 275px;
    }
}

@media only screen and (max-width: 434px) {
    .wrapInfo{
        margin-top: 90px;
    }
    .item-text {
        background-color: white;
    }
    .main__item{
        margin-bottom: 20px;
    }
    .main{
        background-size: 120% 50%;
        background-position: top;
        background-repeat: no-repeat;
        padding-top: 200% !important;
    }
    .detail{
        margin-top: 100vh;
    }
    .main__more{
        height: 100%;
        text-align: center;
    }
    .more__item{
        width: 100%;
        flex-direction: column;
    }
    .more__item div{
        width: 100%;
    }


}
@media only screen and (max-width: 768px) {
    .wrapInfo{


    }
}
@media only screen and (max-width: 1200px) {
    .main{
        background-size: 120% 50%;
        background-position: top;
        background-repeat: no-repeat;

    }
    .item-text {
        background-color: white;
    }
    .wrapInfo{
        margin-top: 145vh;

    }
    .main__more{
        flex-direction: column;
        height: auto;
    }
    .main__more p{
        line-height: 20px;
        width: 100%;
    }
    .main__details{
        margin-top: 90%;
    }
    .aboutAnn{
        margin-bottom: 0;
    }
    .detail{
        margin: 0;
    }
    .instagram_wrap{
        margin-bottom: 0;
    }
    .more__item{
        width: 100%;
        height: 100%;
        text-align: center;
        margin-bottom: 20px;
    }
    .bcg__more{
        width: 50%;
        height: 50%;
    }

    .more__item img{
        width: 100%;
        height: 100%;
    }
    .more__item{
        width: 100%;
        flex-direction: column;
    }
    .more__item div{
        width: 100%;
    }

}
@media only screen and (min-width: 1300px) {
.wrapInfo{
margin-top: 90vh;
}
}